body {
  background: #eee;
  margin-top: 20px;
}

.profile-header {
  position: relative;
  overflow: hidden;
}

.profile-header .profile-header-cover {
  background-image: url("https://media.gettyimages.com/videos/abstract-simple-and-beautiful-green-background-soft-blurred-motion-video-id1284794402?s=640x640");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.profile-header .profile-header-cover:before {
  content: "";
  background: linear-gradient(rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, .75) 100%);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.profile-header .profile-header-content {
  color: #fff;
  padding: 25px;
}

.profile-header-img {
  float: left;
  width: 120px;
  height: 120px;
  z-index: 10;
  background: #fff;
  border-radius: 4px;
  margin: 0 0 -20px;
  padding: 3px;
  position: relative;
  overflow: hidden;
}

.profile-header-img img {
  max-width: 100%;
}

.profile-header-info h4 {
  color: #fff;
  font-weight: 500;
}

.profile-header-img + .profile-header-info {
  margin-left: 140px;
}

.profile-header .profile-header-content, .profile-header .profile-header-tab {
  position: relative;
}

.profile-header .profile-header-tab {
  white-space: nowrap;
  background: #000;
  border-radius: 0;
  margin: -10px 0 0;
  padding: 0 0 0 140px;
  list-style-type: none;
}

.profile-header .profile-header-tab > li {
  margin: 0;
  display: inline-block;
}

.profile-header .profile-header-tab > li > a {
  color: #929ba1;
  border: none;
  padding: 10px 20px;
  font-size: 12px;
  font-weight: 700;
  line-height: 20px;
  text-decoration: none;
  display: block;
}

.profile-header .profile-header-tab > li.active > a, .profile-header .profile-header-tab > li > a.active {
  color: #ffde00;
}

.profile-content {
  border-radius: 4px;
  padding: 25px;
}

.profile-content:after, .profile-content:before {
  content: "";
  clear: both;
  display: table;
}

.profile-content .tab-content, .profile-content .tab-pane {
  background: none;
}

.profile-left {
  width: 200px;
  float: left;
}

.profile-right {
  margin-left: 240px;
  padding-right: 20px;
}

.profile-image {
  height: 175px;
  text-align: center;
  border: 2px solid #e2e7eb;
  border-radius: 4px;
  margin-bottom: 10px;
  font-size: 72px;
  line-height: 175px;
  overflow: hidden;
}

.profile-image img {
  max-width: 100%;
  display: block;
}

.profile-highlight {
  background: #fefde1;
  border-radius: 4px;
  padding: 12px 15px;
}

.profile-highlight h4 {
  margin: 0 0 7px;
  font-size: 12px;
  font-weight: 700;
}

.buttonDisabled {
  background-color: gray;
}

.box {
  height: 11rem;
  width: 11rem;
  background: #000;
  border: .25rem solid #000;
  outline: 0;
  position: relative;
  overflow: hidden;
}

.box:before {
  content: url("logo.4c21e7ca.png");
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.box:after {
  content: "";
  height: 200%;
  width: 200%;
  background: #000;
  border-radius: 80px;
  -webkit-animation: spin 5s ease-in-out infinite;
  animation: spin 5s ease-in-out infinite;
  position: absolute;
  bottom: -50%;
  left: -50%;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: translateY(0)rotate(0deg);
    transform: translateY(0)rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(-100%)rotate(50deg);
    transform: translateY(-100%)rotate(50deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: translateY(0)rotate(0deg);
    transform: translateY(0)rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(-100%)rotate(50deg);
    transform: translateY(-100%)rotate(50deg);
  }
}

.swal2-icon.swal2-error {
  border-color: #f27474 !important;
}

.loader img {
  height: 120px;
  width: 120px;
  -webkit-animation: spinlogo 2s linear infinite;
  animation: spinlogo 2s linear infinite;
}

@-webkit-keyframes spinloader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spinloader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes spinlogo {
  0% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

@keyframes spinlogo {
  0% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

.p-progress-spinner-circle {
  stroke: #ffde00 !important;
}

.button-live {
  color: #eee;
  cursor: pointer;
  text-align: center;
  background-color: #1c87c9;
  border: none;
  border-radius: 60px;
  padding: 5px 15px;
  font-family: sans-serif;
  font-size: 20px;
  text-decoration: none;
  display: inline-block;
}

@-webkit-keyframes glowing {
  0% {
    background-color: #2ba805;
    box-shadow: 0 0 5px #2ba805;
  }

  50% {
    background-color: #49e819;
    box-shadow: 0 0 20px #49e819;
  }

  100% {
    background-color: #2ba805;
    box-shadow: 0 0 5px #2ba805;
  }
}

@keyframes glowing {
  0% {
    background-color: #2ba805;
    box-shadow: 0 0 5px #2ba805;
  }

  50% {
    background-color: #49e819;
    box-shadow: 0 0 20px #49e819;
  }

  100% {
    background-color: #2ba805;
    box-shadow: 0 0 5px #2ba805;
  }
}

.button-live {
  -webkit-animation: glowing 1.3s infinite;
  animation: glowing 1.3s infinite;
}

